import React from "react";
import styles from "./4Chatting.module.css";
import { Client } from "@stomp/stompjs";
import { useState, useEffect, useRef } from "react";
import { useSelector, useDispatch } from "react-redux";
import axios from "axios";
import { chat } from "../api/api";
import { camera_service } from "../api/api";
import logout from "../assets/images/logout2.png";
import Swal from "sweetalert2";
import send from "../assets/images/send.png";


const Chatting = () => {

  // document.getElementById('textarea').addEventListener('keydown', function () {
  //   sendMessage()
  // })
  const [page, setPage] = useState(false); //  /  /  
  const [chatRoomList, setChatRoomList] = useState([]); // 
  const [roomName, setRoomName] = useState(""); //  
  const [roomId, setRoomId] = useState(""); //   
  const [userCount, setUserCount] = useState(0);
  const [message, setMessage] = useState(""); // 
  const [messages, setMessages] = useState([]); //  
  const [createChatVisible, setCreateChatVisible] = useState(false); //   
  const [createChatCheckBox, setCreateChatCheckBox] = useState([]);
  const userList = useSelector((state) => state.userList.userList);
  const user = useSelector((state) => state.user);
  const [userProfileList, setUserProfileList] = useState([]);
  const name = user.name; //  
  const dispatch = useDispatch();
  const client = useRef({});
  useEffect(() => {
    // console.log(userList);
    //       false  
    userList
      .filter((item) => item.email !== user.email)
      .map((item, idx) => {
        setCreateChatCheckBox((createChatCheckBox) => {
          const newArr = [...createChatCheckBox];
          newArr.push(false);
          return newArr;
        });
      });
  }, []);

  async function connect() {
    client.current = new Client();
    client.current.configure({
      brokerURL: "wss://k7c207.p.ssafy.io:8080/api/ws-stomp/websocket",
      onConnect: () => {
        console.log("");
      },
      onChangeState: () => {
        console.log("change");
      },
      onDisconnect: () => {
        console.log("");
      },
      forceBinaryWSFrames: true,
      appendMissingNULLonIncoming: true,
      logRawCommunication: true,
      connectHeaders: {},
      onStompError: function (frame) {
        // Will be invoked in case of error encountered at Broker
        // Bad login/passcode typically will cause an error
        // Complaint brokers will set `message` header with a brief message. Body may contain details.
        // Compliant brokers will terminate the connection after any error
        console.log("Broker reported error: " + frame.headers["message"]);
        console.log("Additional details: " + frame.body);
      },
      debug: function (str) {
        console.log(str);
      },
      reconnectDelay: 5000,
      heartbeatIncoming: 4000,
      heartbeatOutgoing: 4000,
    });
    await client.current.activate();
  }

  //         
  async function findAllRooms() {
    //  
    await axios({
      method: "get",
      url: chat.findAllRooms(),
    })
      .then((res) => {
        // console.log("   ", res.data);

        //          axios 
        //    (id, roomId, roomName,createTime) chatRoomList 
        const newArr = res.data;
        newArr.map((item, idx) => {
          axios({
            method: "get",
            url: chat.getInfo() + `${item.roomId}/${user.email}`,
          })
            .then((result) => {
              console.log("getInfo", result.data);
              if (result.data.userId === user.email) {
                setChatRoomList((chatRoomList) => {
                  const newChatRoomList = [...chatRoomList];
                  newChatRoomList.push(item);
                  return newChatRoomList;
                });
              }
            })
            .catch((e) => {
              console.log(e);
            });
        });
      })
      .catch((e) => {
        console.log(e);
      });
  }

  //     
  // false mySQL  true 
  //Redis  DB subscribe  
  // false mySQL  true 
  //Redis  DB type ENTER publish  
  async function enterRoom(roomId, userId) {
    setRoomId(roomId);
    axios({
      method: "get",
      url: chat.getInfo() + `${roomId}/${userId}`,
    })
      .then((res) => {
        console.log("getInfo", res.data.isEnter, res.data.isSubscribe);
        if (res.data.isSubscribe === false) {
          axios({
            method: "put",
            url: chat.updateSubscribeInfo() + `${roomId}/${userId}/`,
          })
            .then(() => {
              console.log("update Subscribe");
            })
            .catch((e) => {
              console.log(e);
            })
            .finally(() => {
              subscribe(roomId, userId);
            });
        }

        if (res.data.isEnter === false) {
          axios({
            method: "put",
            url: chat.updateEnterInfo() + `${roomId}/${userId}/`,
          })
            .then(() => {
              console.log("update Enter");
            })
            .catch((e) => {
              console.log(e);
            })
            .finally(() => {
              enter(roomId, userId);
            });
        }
      })
      .catch((e) => {
        console.log(e);
      });
  }

  // 
  function sendMessage() {
    client.current.publish({
      destination: "/api/pub/chat/message",
      headers: { id: user.name },
      body: JSON.stringify({
        type: "TALK",
        roomId: roomId,
        sender: user.email,
        message: message,
        userCount: userCount,
        name: user.name,
      }),
    });
    setMessage("");
  }

  //  Redis    
  function recvMessage(recv) {
    setMessages((messages) => {
      const newMessages = [...messages];
      newMessages.push({
        roomId: recv.roomId,
        type: recv.type,
        sender: recv.sender,
        name: recv.name,
        message: recv.message,
      });
      return newMessages;
    });
  }

  //Redis   
  const subscribe = async (roomId, userId) => {
    client.current.subscribe(
      "/api/sub/chat/room/" + roomId,
      (body) => {
        // console.log('body', body.body);
        const recv = JSON.parse(body.body);
        recvMessage(recv);
      },
      { id: user.name }
    );
  };

  // disconnect 
  const disconnect = () => {
    client.current.deactivate();
  };

  const handleChange = (event) => {
    //  state  
    setMessage(event);
  };

  const RoomNameChange = (event) => {
    setRoomName(event);
  };

  // publish     
  async function enter(roomId, userId) {
    await client.current.publish({
      destination: "/api/pub/chat/message",
      headers: {},
      body: JSON.stringify({
        type: "ENTER",
        roomId: roomId,
        sender: userId,
        name: user.name,
      }),
    });
  }

  // publish     
  async function quit(roomId, userId) {
    await client.current.publish({
      destination: "/api/pub/chat/message",
      headers: {},
      body: JSON.stringify({
        type: "QUIT",
        roomId: roomId,
        sender: userId,
        name: user.name,
      }),
    });
  }

  useEffect(() => {
    setChatRoomList([]);
    findAllRooms();
  }, []);

  useEffect(() => {
    connect();
    return () => disconnect();
  }, []);

  //    userList 
  useEffect(() => {
    if (userList.length !== 0) {
      userList
        .filter((item) => item.email !== user.email)
        .map((item) => {
          // console.log(item.email, item.grade, item.id, item.name);
          axios({
            method: "get",
            url: camera_service.getFile(),
            params: {
              userId: item.id,
            },
          })
            .then((res) => {
              console.log("", res.data);

              setUserProfileList((userProfileList) => {
                const newUserProfileList = [...userProfileList];
                newUserProfileList.push({
                  email: item.email,
                  grade: item.grade,
                  id: item.id,
                  name: item.name,
                  profile: res.data,
                  phone: item.phone,
                });
                return newUserProfileList;
              });
            })
            .catch((e) => {
              console.log(e);
            });
        });
    }
  }, [userList]);

  useEffect(() => {
    console.log(userProfileList);
  }, [userProfileList]);

  useEffect(() => {
    setCreateChatVisible(!createChatVisible);
  }, []);

  useEffect(() => {
    console.log(userProfileList);
    console.log(createChatCheckBox);
  }, [createChatCheckBox]);

  function createRoomModal() {
    return (
      <div className={styles.createModal}>
        <div className={styles.modalView}>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <br />
            <input
              style={{ width: "85%", height: "50px", marginTop: "25px", borderRadius: "10px", fontSize: "20px", fontFamily: "NanumSquareNeo-Variable", }}
              type="text"
              autoFocus
              placeholder="   ()"
              onChange={(e) => {
                RoomNameChange(e.target.value);
              }}
              value={roomName}
              maxLength="45"
            ></input>
          </div>
          <hr style={{ width: "90%" }}></hr>
          <div className={styles.createRoomNameLayout}>
            {/*      
                 */}
            {userProfileList
              // .filter((item) => item.email !== user.email)

              .map((item, idx) => {
                return (
                  <div key={idx}>
                    {createChatCheckBox[idx] === false && (
                      <div className={styles.createChatListStyle}>
                        <div
                          style={{
                            cursor: "pointer",
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center",
                          }}
                          onClick={() => {
                            setCreateChatCheckBox((el) => {
                              const newArr = [...el];
                              newArr[idx] = !newArr[idx];
                              return newArr;
                            });
                            setPage(false)
                          }}
                        >
                          <div className={styles.userListDetailText}>
                            <img
                              src={`${item.profile}`}
                              style={{
                                width: "70px",
                                height: "70px",
                                borderRadius: "50%",
                                border: "3px solid #0F1839",
                              }}
                            ></img>
                            <div style={{ marginLeft: "20px" }}>
                              <div>
                                <span
                                  style={{
                                    fontSize: "25px",
                                    fontFamily: "NanumSquareNeo-Variable",
                                  }}
                                >
                                  {item.name}
                                </span>
                                <span
                                  style={{
                                    fontSize: "20px",
                                    fontFamily: "NanumSquareNeo-Variable",
                                  }}
                                >
                                  {item.grade === "MANAGER" && "   ()"}
                                </span>
                              </div>
                              <div className={styles.userListPhone}>
                                {item.phone}
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                );
              })}
            {/*        
                   
                   
                ()
                 .*/}

          </div>

        </div>
      </div>
    );
  }
  return (
    <div className={styles.background}>
      {!createChatVisible && (
        <div style={{ height: "100%", width: "33%", marginRight: "1%" }}>
          <div
            style={{
              fontFamily: "NanumSquareNeo-Variable",
              fontSize: "25px",
              cursor: "pointer",
              width: "100%",
              height: "100%",
              border: "3px solid #0F1839",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              borderRadius: "20px",

            }}
            onClick={() => {
              setCreateChatVisible(!createChatVisible);
            }}
          >
              
          </div>
        </div>
      )}
      {/*     */}
      {createChatVisible && createRoomModal()}

      {/* div */}
      {/* <div className={styles.userListLayout}>
        {userProfileList
          .filter((item) => item.email !== user.email)
          .map((item, idx) => {
            return (
              <div key={idx} className={styles.userList}>
                <div style={{ display: "flex", flexDirection: "column" }}>
                  <img
                    src={`${item.profile}`}
                    style={{ width: "100px", height: "100px", borderRadius: "50%", border: "3px solid #F5CC1F" }}
                  ></img>
                </div>
                <div style={{ display: "flex", justifyContent: "center", flexDirection: "column", marginLeft: "10px", fontFamily: "BMJUA" }}>
                  <div className={styles.userListName}>{item.name}</div>
                  <div className={styles.userListPhone}>{item.phone}</div>
                </div>
              </div>
            );
          })}
      </div> */}
      <div className={styles.chatLayout}>
        {/*  div */}
        {page && (
          <div className={styles.chatRoomLayout2}>

            <div className={styles.chatheadder}>
              <div></div>
              <div style={{ fontSize: "24px" }}> </div>
              <button style={{ background: "none", border: "none", cursor: "pointer", fontFamily: "NanumSquareNeo-Variable", marginRight: "10px", color: "white" }}
                onClick={() => {
                  setPage(false);
                }}
              >
                
              </button>
            </div>

            <div className={styles.chatRoomLayout}>
              <div className={styles.chatOutDiv}>
                {messages.map((item, idx) => {
                  return (
                    <div key={idx}>
                      {/* messages  roomId   roomId 
                  sender    user    UI(flex-direction : row-reverse)
                  sender    user   UI(flex-direction : row)*/}
                      <div
                        className={
                          item.roomId === roomId && item.sender === user.email
                            ? styles.myChatComponent
                            : item.roomId === roomId && item.sender !== user.email
                              ? styles.otherChatComponent
                              : {}
                        }
                      >
                        {/* messages  roomId   roomId 
                    type 'TALK'  sender      () */}
                        {item.roomId === roomId &&
                          item.type === "TALK" &&
                          item.sender !== user.email && (
                            <div className={styles.otherChatProfile}></div>
                          )}
                        {/*  */}
                        <div style={{ width: "100%" }}
                          className={
                            (item.type === "ENTER" || item.type === "QUIT") &&
                            styles.noticeChatLayout
                          }
                        >
                          {/* messages  roomId   roomId 
                        type 'TALK'  sender      
                       */}
                          {item.roomId === roomId &&
                            item.type === "TALK" &&
                            item.sender !== user.email && (
                              <div className={styles.otherChatName}>
                                {item.name}
                              </div>
                            )}

                          {/* messages  roomId   roomId  
                        type 'ENTER' noticeChat 
                        sender    myChat 
                        sender     otherChat  
                      */}
                          <div
                            className={
                              item.roomId === roomId && item.type === "ENTER"
                                ? styles.noticeChat
                                : item.roomId === roomId &&
                                  item.sender === user.email
                                  ? styles.myChat
                                  : item.roomId === roomId &&
                                    item.sender !== user.email
                                    ? styles.otherChat
                                    : {}
                            }
                          >
                            {/* messages  roomId   roomId 
                        type 'ENTER'    */}
                            {item.roomId === roomId && item.type === "ENTER" && (
                              <div className={styles.noticeChatText}>
                                {item.name} .
                              </div>
                            )}

                            {/* messages  roomId   roomId 
                        type 'QUIT'     */}
                            {item.roomId === roomId && item.type === "QUIT" && (
                              <div className={styles.noticeChatText}>
                                {item.name} .
                              </div>
                            )}

                            {/* messages  roomId   roomId 
                        type 'TALK'  sender      */}
                            {item.roomId === roomId && item.type === "TALK" && (
                              <div
                                className={
                                  item.sender === user.email
                                    ? styles.myChatText
                                    : styles.otherChatText
                                }
                              >
                                {item.message}
                              </div>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>

            </div>
            {/*      */}
            <div className={styles.bottomContainer}>
              <hr style={{ width: "95%" }} />
              <div style={{ display: "flex", justifyContent: "center", marginBottom: "0px" }}>
                <textarea id="textarea"
                  style={{ width: "85%", marginLeft: "1%", marginRight: "1%", marginTop: "1%", marginBottom: "1%", borderRadius: "10px", height: "50px", fontSize: "20px", fontFamily: "NanumSquareNeo-Variable", }}
                  className={styles.messageInput}
                  placeholder={"  "}
                  onChange={(e) => {
                    handleChange(e.target.value);
                  }}
                  value={message}
                ></textarea>
                <button style={{ marginTop: "1%", marginBottom: "1%", background: "none", border: "none", cursor: "pointer", backgroundColor: "#0F1839", borderRadius: "10px", fontFamily: "NanumSquareNeo-Variable", }}
                  clsssName={styles.buttonStyle}
                  onClick={sendMessage}
                  disabled={message === ""}
                >
                  <img src={send} style={{ width: "30px" }} alt="" />
                </button>
              </div>
            </div>

          </div>
        )}

        {!page && (
          <div
            style={{ width: "50%", height: "100%", marginRight: "1.5%" }}>
            <div
              style={{
                // backgroundColor: "blue",
                width: "100%",
                height: "100%",
                border: "3px solid #0F1839",
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center",
                borderRadius: "20px",
                fontFamily: "NanumSquareNeo-Variable",
                fontSize: "25px",
              }}
            >
              {/*  createChatcheckBox true   */}
              {!createChatCheckBox.includes(true) && (
                <div style={{ height: "100%", display: "flex", justifyContent: "center", alignItems: "center" }}
                // onClick={() => {
                //   setCreateChatVisible(!createChatVisible);
                // }}
                >    .</div>
              )}
              {(createChatCheckBox.includes(true) && <div style={{ width: "100%", height: "100%", display: "flex", flexDirection: "column" }}>

                <div className={styles.createRoomNameLayout2}>
                  {userProfileList
                    // .filter((item) => item.email !== user.email)

                    .map((item, idx) => {
                      return (
                        <div key={idx} style={{}}>
                          {createChatCheckBox[idx] === true && (
                            <div className={styles.createChatListStyle2}>
                              <div
                                style={{
                                  cursor: "pointer",
                                  display: "flex",
                                  justifyContent: "center",
                                  alignItems: "center",
                                }}
                                onClick={() => {
                                  setCreateChatCheckBox((el) => {
                                    const newArr = [...el];
                                    newArr[idx] = !newArr[idx];
                                    return newArr;
                                  });
                                  console.log(createChatCheckBox)
                                }}
                              >
                                <div className={styles.userListDetailText2}>
                                  <img
                                    src={`${item.profile}`}
                                    style={{
                                      width: "70px",
                                      height: "70px",
                                      borderRadius: "50%",
                                      border: "3px solid #0F1839",
                                    }}
                                  ></img>
                                  <div style={{ marginLeft: "20px" }}>
                                    <div>
                                      <span
                                        style={{
                                          fontSize: "25px",
                                          fontFamily: "NanumSquareNeo-Variable",
                                        }}
                                      >
                                        {item.name}
                                      </span>
                                      <span
                                        style={{
                                          fontSize: "20px",
                                          fontFamily: "NanumSquareNeo-Variable",
                                        }}
                                      >
                                        {item.grade === "MANAGER" &&
                                          "   ()"}
                                      </span>
                                    </div>
                                    <div className={styles.userListPhone}>
                                      {item.phone}
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          )}
                        </div>
                      );
                    })}
                </div>
                {/*   */}
                {/*      */}
                <div className={styles.buttonLayout}>
                  <hr style={{ width: "90%" }} />
                  <div
                    style={{
                      display: "flex",
                      width: "100%",
                      // backgroundColor: "black",
                      justifyContent: "center",
                      marginTop: "10px",
                      marginBottom: "10px",
                    }}
                  >

                    <button
                      onClick={async () => {
                        if (!roomName) {
                          Swal.fire({
                            icon: 'error',
                            title: '  ',
                            confirmButtonText: '',
                            confirmButtonColor: '#0F1839'
                          })
                        }
                        if (roomName !== "") {
                          await axios({
                            method: "post",
                            url: chat.createRoom(),
                            params: {
                              name: roomName,
                              userId: user.email,
                            },
                          })
                            .then((res) => {
                              createChatCheckBox.map((item, idx) => {
                                if (item === true) {
                                  axios({
                                    method: "post",
                                    url:
                                      chat.invite() +
                                      `${res.data.roomId}/${userProfileList[idx].email}/`,
                                  })
                                    .then((res) => {
                                      console.log("", res.data);
                                    })
                                    .catch((e) => {
                                      console.log(e);
                                    });
                                }
                              });
                            })
                            .catch((e) => {
                              console.log(e);
                              console.log("?")
                            });
                          createChatCheckBox.map((item, idx) => {
                            if (item === true) {
                              setCreateChatCheckBox((createChatCheckBox) => {
                                const newArr = [...createChatCheckBox];
                                newArr[idx] = false;
                                return newArr;
                              });
                            }
                          });

                          setRoomName("");
                          setChatRoomList([]);
                          findAllRooms();
                        }
                      }}
                      style={{
                        width: "120px",
                        height: "60px",
                        border: "none",
                        backgroundColor: "#F5CC1F",
                        cursor: "pointer",
                        borderRadius: "10px",
                        fontSize: "25px",
                        marginRight: "5%",
                        fontFamily: "NanumSquareNeo-Variable",
                      }}
                    >
                      
                    </button>
                    <button
                      style={{
                        width: "120px",
                        height: "60px",
                        border: "none",
                        backgroundColor: "#0F1839",
                        cursor: "pointer",
                        color: "white",
                        borderRadius: "10px",
                        fontSize: "25px",
                        marginLeft: "5%",
                        fontFamily: "NanumSquareNeo-Variable",
                      }}
                      onClick={() => {
                        setCreateChatVisible(!createChatVisible);
                        createChatCheckBox.map((item, idx) => {
                          if (item === true) {
                            setCreateChatCheckBox((createChatCheckBox) => {
                              const newArr = [...createChatCheckBox];
                              newArr[idx] = false;
                              return newArr;
                            });
                          }
                        });
                      }}
                    >
                      
                    </button>
                  </div>
                </div>
                {/*   */}
              </div>)}
            </div>
          </div>
        )}

        {/*   div */}
        <div className={styles.chatRoomListLayout}>

          {/*    */}
          <div style={{ paddingTop: "20px" }}>
            {chatRoomList.map((item, idx) => {
              // console.log(chatRoomList)
              return (
                <div key={idx} className={styles.chatRoomList}>
                  <button style={
                    {
                      width: "85%",
                      background: "none",
                      border: "none",
                      paddingTop: "5px",
                      paddingBottom: "5px",
                      fontSize: "20px",
                      marginLeft: "20px",
                      textAlign: "start",
                      cursor: "pointer",
                      fontFamily: "NanumSquareNeo-Variable"
                    }}
                    onClick={() => {
                      setPage(true);
                      // console.log(
                      //   "enterRoom  ",
                      //   item.roomId,
                      //   user.email
                      // );
                      enterRoom(item.roomId, user.email);
                    }}
                  >
                    {item.roomName}
                  </button>
                  <button
                    style={{ background: "none", border: "none", cursor: "pointer" }}
                    onClick={() => {
                      setPage(false);
                      axios({
                        method: "delete",
                        url: chat.exitRoom() + `${item.roomId}/${user.email}`,
                      })
                        .then((res) => {
                          // console.log(' ', res.data);
                          quit(item.roomId, user.email);
                        })
                        .catch((e) => {
                          console.log(e);
                        });
                      setChatRoomList([]);
                      findAllRooms();
                    }}
                  >
                    <img src={logout} style={{ width: "30px", marginTop: "10px", marginBottom: "10px" }} alt="" />
                  </button>
                </div>
              );
            })}
          </div>

          {!chatRoomList.length && <div
            style={{
              height: "100%",
              display: "flex",
              fontSize: "25px",
              fontFamily: "NanumSquareNeo-Variable",
              justifyContent: "center",
              alignItems: "center",

            }}
          >
              .
          </div>}

        </div>
      </div>
    </div>
  );
};

export default Chatting;
